import React from 'react';
import {NavLink} from 'react-router-dom';
import './NavLinks.css';

export default function NavLinks(props){
    const scrollToRef = (ref) => {
        var headerOffset = 128;
        var elementPosition = ref.current.getBoundingClientRect().top;
        var offsetPosition = elementPosition - headerOffset + window.pageYOffset;
        window.scrollTo({
            top: offsetPosition,
            behavior: "smooth"
        });
    }

    return <ul className="nav-links">
        <li onClick={()=>scrollToRef(props.pagerefs[0])}><a>Home</a></li>
        <li onClick={()=>scrollToRef(props.pagerefs[1])}><a>About Us</a></li>
        <li onClick={()=>scrollToRef(props.pagerefs[2])}><a>Contact</a></li>
        <li onClick={()=>scrollToRef(props.pagerefs[3])}><a>Reports</a></li>
    </ul>
}