import React from 'react';
import './Card.css'

export default function Card(props){
    return(
        <div className="card">
            <div className="card__image-text-wrapper">
                <img src={props.imagesrc} alt="image" className="card__image" />
            <p className="card__text">{props.text}</p>
            </div>
        </div>
    )
}